	module.exports = {"data":{"markdownRemark":{"html":"<h2>Storyboards</h2>\n<p>A storyboard is a sequence of images and accompanying blurbs that together convey a user’s experience in accomplishing some goal. Storyboards help Product and UX teams brainstorm around a user’s pain points. </p>\n<p>Storyboarding first comes after user research, persona development, and jobs-to-be-done. This instance of Storyboarding outlines the user’s pain points with the existing solution. The next instance of storyboarding comes after the solution ideation, to communicate the envisioned future of how the user’s life has been improved.</p>\n<p>Generally sketched by hand, Storyboards are a quick and cheap way to communicate a user’s behaviour before and after the proposed solution.</p>\n<p>Effective Storyboards…</p>\n<ul>\n<li>\n<p>Should: </p>\n<ul>\n<li>clearly outline the User, their Actions, Emotions, and Outcomes</li>\n<li>show only UI elements relevant to the interaction when showing device close-ups</li>\n</ul>\n</li>\n<li>\n<p>Should NOT: </p>\n<ul>\n<li>be “pixel-perfect”</li>\n<li>demonstrate UI completeness</li>\n</ul>\n</li>\n</ul>\n<p><img src=\"https://github.com/yunChigewan/storage/blob/master/storyboard_pic.jpeg?raw=true\" alt=\"Storyboard Example\"></p>\n<p> Image credit: Chelsea Hostetter, Austin Center for Design</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li>Stock images to use in Storyboards www.designcomics.org</li>\n<li>Storyboarding article from UX Planet <a href=\"https://uxplanet.org/storyboarding-in-ux-design-b9d2e18e5fab\">https://uxplanet.org/storyboarding-in-ux-design-b9d2e18e5fab</a></li>\n</ul>","fields":{"slug":"/user-experience-design/storyboards/"},"frontmatter":{"title":"Storyboards"}}},"pathContext":{"slug":"/user-experience-design/storyboards/"}}

